<template>
    <div class="bb">
        <i class="iconfont ICO" @click="rollclick(2)">&#xe62a;</i>
        <div class="Mask" @click="mask"></div>
        <div class="hearer">
            <div class="hearerone">
                <img src="/static/mimg/left.png" alt="" class="hearer1">
                <img src="/static/mimg/right.png" alt="" class="hearer2" @click="head">
            </div>
        </div>
        <div class="middle">
            <ul class="middletwo">
                <li @click="rollclick(1)">Home</li>
                <li @click="rollclick(2)">HRC</li>
                <li @click="rollclick(6)">Scenes</li>
                <li @click="rollclick(7)">Architecture</li>
                <li @click="rollclick(5)">Blog</li>
                <li @click="rollclick(3)">Character</li>
                <li @click="rollclick(4)">Broker</li>
            </ul>
        </div>
        <div class="aa">
            <img src="/static/mimg/hhpps.png" alt="" class="cc" id="nav1">
            <p class="lmore">learn more</p>
        </div>
        <div class="mhome">
            <div class="m-header">
                <img src="/static/mimg/111.png" alt="" id="nav2">
                <img src="/static/mimg/222.png" alt="" id="nav6">
                <img src="/static/mimg/333.png" alt="" id="nav7">
                <img src="/static/mimg/HHPP2.png" alt="" class="m-h1" @click="loadinghref" id="nav5">
                <img src="/static/mimg/h.png" alt="" id="nav3">
                <img src="/static/mimg/qq.png" alt="" id="nav4">
            </div>
            <div class="mbottom">
                <div>
                    <a href="https://telegr.am/">
                        <img src="/static/mimg/dian.png" alt="">
                    </a>
                </div>
                <div>
                    <a href="https://www.facebook.com/UsechainFoundation">
                        <img src="/static/mimg/fa.png" alt="">
                    </a>
                </div>
                <div>
                    <a href="https://twitter.com/usechain">
                        <img src="/static/mimg/face.png" alt="">
                    </a>
                </div>
                <div>
                    <a href="https://github.com/">
                        <img src="/static/mimg/github.png" alt="">
                    </a>
                </div>
            </div>
            <div class="mba">
                <a href="/static/whitepaper.pdf" download="whitepaper.pdf">
                    <img src="/static/mimg/down.png" alt="">
                </a>
            </div>

        </div>
    </div>
</template>
<script>
    export default {
        name: 'mhome',
        data() {
            return {
                istrue: true,
                navnum: 0,
            }
        },
        created() {
            this.setRem(28.43);
            this.fontSize(1120);
            let useragent = navigator.userAgent.toLowerCase()
            let ismobile = /(iphone|ipod|ipad|android)/.test(useragent)
            if (!ismobile) {
                this.$router.push({ path: '/' })
            }
            // let height12=window.innerHeight||document.documentElement.clientHeight||document.body.clientHeight
            // $(".aa").css("height", (height12)+'px')
            // $(".cc").css("height", (height12)+'px')
            // $(".aa").height(document.body.offsetHeight)
            // $(".bb").height(100)
        },
        methods: {
            rollclick(index) {
                this.istrue = !this.istrue
                document.getElementsByClassName("middle")[0].style.right = "-100%"
                $(".Mask").fadeOut(1000)
                this.navnum = index
                let mtop = $(`#nav` + index)[0].offsetTop
                $("html,body").animate({
                    scrollTop: mtop
                }, 500);
            },
            head() {
                this.istrue = !this.istrue
                document.getElementsByClassName("middle")[0].style.right = 0
                $(".Mask").fadeIn(1000)
            },
            mask() {
                document.getElementsByClassName("middle")[0].style.right = "-100%"
                $(".Mask").fadeOut(1000)
            },
            loadinghref() {
                this.$router.push({ path: '/mblok' })
                location.reload()
            },
            setRem(itemNum) {
                //设置字体rem
                let size = itemNum || 38;
                window.onresize = remSize;
                remSize();
                function remSize() {
                    let html = document.getElementsByTagName("html")[0],
                        w = html.clientWidth;
                    w = w > 768 ? 768 : w;
                    html.style.fontSize = w / size + "px";
                }
            },
            fontSize(itemnum) {
                let html = document.getElementsByTagName("html")[0];
                let a = html.style.fontSize;
            },
        }
    }
</script>
<!-- Add "scoped" attribute to limit CSS to this component only -->
<style>
    @font-face {
        font-family: 'iconfont';
        /* project id 538230 */
        src: url('//at.alicdn.com/t/font_538230_dzzii2d47qu.eot');
        src: url('//at.alicdn.com/t/font_538230_dzzii2d47qu.eot?#iefix') format('embedded-opentype'),
        url('//at.alicdn.com/t/font_538230_dzzii2d47qu.woff') format('woff'),
        url('//at.alicdn.com/t/font_538230_dzzii2d47qu.ttf') format('truetype'),
        url('//at.alicdn.com/t/font_538230_dzzii2d47qu.svg#iconfont') format('svg');
    }

    .iconfont {
        font-family: "iconfont" !important;
        font-size: 16px;
        font-style: normal;
        -webkit-font-smoothing: antialiased;
        -webkit-text-stroke-width: 0.2px;
        -moz-osx-font-smoothing: grayscale;
    }

    * {
        margin: 0;
        padding: 0;
    }

    body {
        margin: 0 !important
    }

    .m-header {
        height: 100%;
        width: 100%;
    }

    .m-header img {
        width: 100%;
        height: 100%
    }

    .m-header .m-h1 {
        margin-top: -0.2rem;
    }

    .mhome .mbottom {
        height: 5rem;
        width: 100%;
        background-color: #202835;
        display: flex;
        box-sizing: border-box;
        padding: 1rem 2rem;
    }

    .mhome .mbottom>div {
        flex: 1;
        position: relative;
    }

    .mhome .mbottom>div>a {
        width: 50%;
        height: 2rem;
    }

    .mhome .mbottom>div>a>img {
        width: 50%;
        height: 100%;
        position: absolute;
        left: 50%;
        transform: translateX(-50%)
    }

    .mhome .mba {
        width: 6rem;
        height: auto;
        text-align: center;
        position: fixed;
        left: 0;
        bottom: 6rem;
    }

    .mhome .mba>a {
        width: 100%;
        height: 100%
    }

    .mhome .mba>a>img {
        width: 100%;
        height: 100%
    }

    html {
        margin: 0 !important;
        width: 100%;
        height: 100%;
    }

    body {
        margin: 0 !important;
        width: 100%;
        height: 100%;
    }

    #app {
        width: 100%;
        height: 100%;
    }

    .bb {
        width: 100%;
        height: 100%;
    }

    .bb .aa {
        width: 100%;
        height: 100%;
    }

    .bb .aa>img {
        width: 100%;
        height: 100%
    }

    .hearer {
        width: 100%;
        /* height: 2rem; */
        /* background-color: red */
        position: absolute;
        z-index: 12321;
    }

    .hearer .hearerone {
        width: 100%;
        height: 1.8rem;
        /* background-color: red */
        /* position: absolute;
        z-index: 12321; */
    }

    .hearer .hearer1 {
        width: 4rem;
        height: 100%;
        float: left;
        margin-left: 1.5rem;
        margin-top: 1rem;
    }

    .hearer .hearer2 {
        width: 2.5rem;
        height: 100%;
        float: right;
        margin-right: 1.5rem;
        margin-top: 1rem;
    }

    .hearer .middle {
        /* position: absolute;
        z-index: 12321; */
        margin-top: 2rem;
        width: 100%;
    }

    .bb .middle ul {
        width: 100%;

    }

    .bb .middle ul li {
        width: 100%;
        height: 3rem;
        background-color: rgba(255, 255, 255, 0.95);
        text-align: center;
        line-height: 3rem;
        border: 0px
    }

    #nav4 {
        margin-top: -0.5rem
    }

    /* 
    .aa .lmore {
        position: absolute;
        top: 0;
        left: 0;
        background-color: red;
    } */

    .bb .middle {
        width: 71%;
        height: 100%;
        position: fixed;
        background-color: rgba(255, 255, 255, 0.95);
        right: -100%;
        box-sizing: border-box;
        top: 0;
        transition: all 0.4s linear;
        z-index: 3;
        -webkit-transition: all 0.4s linear;
        -moz-transition: all 0.4s linear;
        -o-transition: all 0.4s linear;
    }

    .Mask {
        width: 100%;
        height: 100%;
        background-color: rgba(0, 0, 0, 0.5);
        position: fixed;
        top: 0;
        z-index: 2;
        display: none;
    }

    .aa .lmore {
        position: absolute;
        top: 88%;
        right: 40%;
        cursor: pointer;
        color: rgba(182, 177, 177, 0.5);
        animation: change1 1.5s ease-in infinite;
    }

    @keyframes change {
        0% {
            text-shadow: 0 0 4px #f00
        }
        50% {
            text-shadow: 0 0 40px #f00
        }
        100% {
            text-shadow: 0 0 4px #f00
        }
    }
    .bb .ICO {
        position: absolute;
        left: 0;
        bottom: 0;
        left: 47%;
        color: rgba(182, 177, 177, 0.5);
        font-size: 26px;
        top: 93%;
        animation: change1 1.5s ease-in infinite;
    }
    @keyframes change1 {
        0% {
            opacity: 0
        }
        50% {
            opacity: 1
        }
        100% {
            opacity: 0
        }
    }
</style>


